.hamburger-menu {
   min-height: 100vh;
   width: 100%;
   position: fixed;
   top: 0;
   left: 0;
   background-color: #de4f4f;
   z-index: 99;
   display: flex;
   flex-direction: column;
}
.hamburger-menu--list {
   flex-grow: 1;
   margin: 0;
   padding: 15px 0;
   list-style: none;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}
.hamburger-menu--item {
   margin-bottom: 10px;
   &:last-child {
      margin-bottom: 0;
   }
}
.hamburger-menu--button {
   padding: 0;
   background: transparent;
   border: 0;
   color: #fff;
   font-size: 29px;
   font-weight: 700;
   letter-spacing: 0.63px;
   line-height: 50px;
   text-transform: uppercase;
   cursor: pointer;
}