.contacts-modal {
   * {
      font-family: 'Open Sans', sans-serif !important;
   }
   position: relative;
   & > div > div {
      @media (max-width: 450px) {
         margin: 0 10px;
      }
   }
   .contacts-modal--close {
      position: absolute;
      top: 15px;
      right: 15px;
      padding: 18px;
      @media (max-width: 450px) {
         padding: 16px;
      }
      &:before,
      &:after {
         content: '';
         position: absolute;
         height: 2px;
         width: 28px;
         top: 50%;
         left: 0;
         margin: -1px 0 0 4px;
         background: #000;
         @media (max-width: 450px) {
            width: 24px;
         }
      }
      &:before {
         transform: rotate(45deg);
      }
      &:after {
         transform: rotate(-45deg);
      }
   }
   .contacts-modal--title {
      padding-bottom: 15px;
      @media (min-width: 451px) {
         text-align: center;
      }
      h2 {
         font-weight: 700;
      }
   }
   .contacts-modal--body {
      padding-top: 15px;
   }
   .contacts-modal--input {
      margin-bottom: 15px;
   }
   .contacts-modal--submit {
      border-radius: 20px;
      padding: 7px 25px;
      line-height: 26px;
      border: 0;
      box-shadow: 0 5px 20px 0 rgba(203,55,55,.3);
      margin: 10px 0 0 auto;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 16px;
      color: #fff;
      min-width: 150px;
      position: relative;
      overflow: hidden;
      z-index: 1;
      &:before {
         content: '';
         width: 150%;
         height: 100%;
         position: absolute;
         left: 0;
         top: 0;
         background-image: linear-gradient(90deg,#de3535 0%,#e3621d 80%, #e3621d 100%);
         z-index: -1;
         transition: transform .2s ease-in-out;
      }
      &:hover:before {
         transform: translateX(-33%);
      }
      svg {
         fill: #fff;
         margin-left: 10px;
      }
   }
}