.button {
   padding: 0.9375rem;
   min-width: 230px;
   color: #de4f4f;
   font-size: 1rem;
   font-weight: 700;
   letter-spacing: 0.35px;
   text-transform: uppercase;
   border: 2px solid currentColor;
   cursor: pointer;
   position: relative;
   background-color: transparent;
   z-index: 1;
   overflow: hidden;
   &:hover {
      .button--blur {
         background-color: rgba(222, 79, 79, 0.4);
      }
   }
   &:active {
      box-shadow: none;
   }
   .button--blur {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(222, 79, 79, 0.35);
      transition: background-color .1s ease-in-out;
      filter: blur(5px);
   }
   .button--active-effect {
      position: absolute;
      transform: translate(-50%, -50%);
      background: rgba(#fff, .2);
      z-index: -1;
      animation: buttonClick .5s ease-in-out;
      display: block;
      border-radius: 50%;
   }
}
@keyframes buttonClick {
   from {
      width: 0;
      height: 0;
   }
   to {
      width: 500px;
      height: 500px;
   }
}