.social-icons {
   position: absolute;
   right: 10px;
   top: 50%;
   transform: translateY(-50%);
   z-index: 20;
   &.-is-hamburger {
      position: static;
      transform: none;
      padding: 15px 0;
      .social-icons--list {
         flex-direction: row;
         justify-content: center;
      }
      .social-icons--item {
         margin: 0 7px 0 0;
      }
      .social-icons--link {
         &:before,
         &:after {
            display: none;
         }
         &:hover {
            #Artboard {
               fill: #fff;
            }
         }
         .social-icons--link-title {
            display: none;
         }
      }
   }
}
.social-icons--list {
   display: flex;
   flex-direction: column;
   margin: 0;
   padding: 0;
   list-style: none;
   align-items: flex-end;
}
.social-icons--item {
   margin-bottom: 10px;
   font-size: 0;
   &:last-child {
      margin: 0;
   }
}
.social-icons--link {
   display: flex;
   align-items: center;
   padding: 2px .5px;
   border-radius: 2px;
   text-decoration: none;
   position: relative;
   overflow: hidden;
   font-style: normal;
   &:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translate(50%, -50%);
      right: 16px;
      width: 0;
      height: 0;
      border-radius: 50%;
      z-index: -1;
   }
   &:hover {
      &:before {
         width: 230px;
         height: 230px;
         transition: width .2s ease-in, height .2s ease-in;
      }
      .social-icons--link-title {
         display: block;
         animation: showText .3s linear;
      }
      #Artboard {
         fill: #3e47ca;
      }
   }
   &.light:before {
      background: #fff;
   }
   &.dark:before {
      background: rgba(#f5caca, .8);
   }
}
.social-icons--link-title {
   font-size: 14px;
   margin: 0 10px;
   display: none;
   color: #12233d;
}
@keyframes showText {
   from {
      opacity: 0;
   }
   75% {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}