.our-appoach-portfolio-section {
   height: 100vh;
   position: relative;
   z-index: 1;
   overflow: hidden;
   background: #fff;
   .landing-page-content-wrap {
      .container {
         position: relative;
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
      }
   }
}
.our-appoach-portfolio {
   padding: 30px 0;
}
.our-appoach-portfolio--list {
   margin: -10px;
   padding: 0;
   list-style: none;
}
.our-appoach-portfolio--item {
   padding: 10px;
   &.-more {
      color: #12233d;
      font-size: 22px;
      min-height: 50px;
      text-align: center;
      align-items: center;
      display: flex;
      justify-content: center;
      position: relative;
      @media (max-width: 800px) {
         font-size: 19px;
      }
      &:before {
         content: '';
         position: absolute;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         width: calc(100% - 30px);
         height: 50%;
         border-radius: 3px;
         border: 1px solid rgba(#de4f4f, .5);
      }
      a {
         text-decoration: none;
         margin-left: 0.3em;
         position: relative;
         color: #12233d;
         &:before {
            content: '';
            position: absolute;
            width: 100%;
            bottom: 10%;
            left: 0;
            height: 15%;
            background-color: rgba(#de4f4f, .25);
            transition: height .1s ease-in-out, background-color .1s ease-in-out;
            z-index: -1;
         }
         &:hover:before {
            height: 60%;
            background-color: rgba(#de4f4f, .15);
         }
      }
   }
}
.our-appoach-portfolio--link {
   color: #fff;
   display: block;
   max-width: 350px;
   margin: 0 auto;
   text-decoration: none;
   @media screen and (max-width: 650px) {
      color: #12233d;
   }
}
.our-appoach-portfolio--figure {
   position: relative;
   margin: 0;
   font-size: 0;
   @media screen and (max-width: 650px) {
      display: flex;
      flex-direction: column;
   }
}
.our-appoach-portfolio--image {
   width: 100%;
}
.our-appoach-portfolio--figcaption {
   padding: 30px 15px;
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
   display: flex;
   flex-direction: column;
   background-color: rgba(#051c4f, .3);
   transition: background-color .1s ease-in-out;
   cursor: pointer;
   &:hover {
      @media screen and (min-width: 601px) {
         background-color: rgba(#051c4f, .5);
      }
   }
   @media screen and (max-width: 700px) {
      padding: 15px;
   }
   @media screen and (max-width: 650px) {
      position: static;
      padding: 15px 5px;
      background: transparent;
   }
}
.our-appoach-portfolio--title {
   font-size: 1.5rem;
   font-weight: 700;
   letter-spacing: 0.52px;
   text-transform: uppercase;
   margin: 0 0 10px 0;
   line-height: .9;
   @media screen and (max-width: 700px) {
      font-size: 1.2rem;
      margin-bottom: 7px;
   }
}
.our-appoach-portfolio--description {
   font-size: 1.25rem;
   font-weight: 600;
   letter-spacing: 0.43px;
   margin: 0 0 5px 0;
   flex-grow: 1;
   @media screen and (max-width: 700px) {
      font-size: 1rem;
   }
}
.our-appoach-portfolio--technology-list {
   display: flex;
   flex-wrap: wrap;
   margin: -1px -3px;
   padding: 0;
   list-style: none;
}
.our-appoach-portfolio--technology-item {
   padding: 1px 3px;
   color: #fff;
   font-size: 1rem;
   font-weight: 600;
   letter-spacing: 0.35px;
   @media screen and (max-width: 700px) {
      font-size: 0.85rem;
   }
}
.our-appoach-portfolio--button-wrap {
   display: block;
   text-align: center;
}