.counter-wrap {
	display: flex;
	flex-direction: column;
	padding: 15px;
	white-space: nowrap;
}
.counter {
	color: #de4f4f;
	font-size: 50px;
	line-height: 60px;
	font-weight: 600;
	display: flex;
	height: 60px;
	overflow: hidden;
	font-size: 3.375rem;
	@media screen and (max-width: 850px) {
      font-size: 2.8rem;
   }
	&:before {
		top: 0;
		background: linear-gradient(to bottom, rgba(252, 252, 252, 1) 0%, rgba(252, 252, 252, 0) 100%);
	}
	&:after {
		background: linear-gradient(to top, rgba(252, 252, 252, 1) 0%, rgba(252, 252, 252, 0) 100%);
		bottom: 0;
	}
}
.counter-title {
	color: #de4f4f;
   font-size: 1.25rem;
   letter-spacing: 0.43px;
   line-height: 30px;
	max-width: 125px;
	white-space: normal;
   @media screen and (max-width: 850px) {
      font-size: 1.15rem;
   }
   @media screen and (max-width: 500px) {
      font-size: 1rem;
   }
}
.counter-column {
	display: flex;
	flex-direction: column;
	transition: transform 2s ease;
	transform: translateY(0);
	&.-character {
		justify-content: flex-end;
		height: 660px;
	}
}