.horizontal-slider {
   width: 100%;
}
.horizontal-slider--list {
   display: flex;
   padding: 0;
   margin: 0;
   list-style: none;
   transition: transform .2s linear;
}
.horizontal-slider-button-controls {
   display: flex;
   padding: 0 15px;
   margin: 0;
}
.horizontal-slider-button-controls--item {
   margin: 15px 0 0 0;
   padding: 0;
   list-style: none;
   flex-grow: 1;
   &.-active {
      .horizontal-slider-button-controls--btn {
         border-color: #de4f4f;
         color: #de4f4f;
      }
   }
   &.-more {
      width: 220px;
      flex-grow: 0;
      @media screen and (max-width: 600px) {
         width: 160px;
      }
      @media screen and (max-width: 400px) {
         width: 140px;
      }
      .horizontal-slider-button-controls--btn {
         background: transparent;
         color: rgba(#fff, 0.26);
         text-align: center;
         cursor: default;
         @media screen and (max-width: 400px) {
            padding: 10px 5px;
         }
         a {
            color: rgba(#fff, 0.6);
            text-decoration: none;
            position: relative;
            transition: color .1s ease-in-out;
            &:before {
               content: '';
               position: absolute;
               width: 100%;
               bottom: 10%;
               left: 0;
               height: 15%;
               background-color: rgba(#de4f4f, .3);
               transition: height .1s ease-in-out, background-color .1s ease-in-out;
               z-index: -1;
            }
            &:hover {
               color: rgba(#fff, 0.7);
               &:before {
                  height: 60%;
               }
            }
         }
      }
   }
}
.horizontal-slider-button-controls--btn {
   padding: 15px;
   width: 100%;
   cursor: pointer;
   background: transparent;
   border: 0;
   border-top: 2px solid #fff;
   color: rgba($color: #fff, $alpha: .26);
   font-size: 1.5rem;
   font-weight: 700;
   letter-spacing: 0.52px;
   transition: background-color .1s ease-in-out;
   &:hover {
      background-color: rgba(222, 79, 79, 0.15);
   }
   @media screen and (max-width: 600px) {
      font-size: 1.2rem;
      padding: 10px;
   }
}
.horizontal-slider-arrow-controls {
   display: flex;
   padding: 20px 0;
   margin: 0;
   justify-content: space-around;
}
.horizontal-slider-arrow-controls--btn {
   width: 30%;
   border: 0;
   padding: 5px 10px;
   background: transparent;
   position: relative;
   cursor: pointer;
   color: #d62c2c;
   span {
      display: block;
      height: 1px;
      background: currentColor;
   }
   &:before,
   &:after {
      content: '';
      width: 8px;
      height: 1px;
      top: 50%;
      background-color: currentColor;
      position: absolute;
   }
   &:before {
      transform: translateY(-100%) rotate(45deg);
   }
   &:after {
      transform: translateY(-100%) rotate(-45deg);
   }
   &.-prew {
      &:before,
      &:after {
         left: 0;
         transform-origin: 0%;
      }
   }
   &.-next {
      &:before,
      &:after {
         right: 0;
         transform-origin: 100%;
      }
   }
   &.-inactive {
      color: #cbcbcb;
   }
}