.wave-background {
   position: absolute;
   bottom: 0;
   left: 50%;
   transform: translateX(-50%);
   overflow: hidden;
}
.wave-background--big {
   animation: bigWave 10s ease-in-out infinite alternate;
   width: 100vw;
   height: 12.88vw;
}
.wave-background--small {
   position: absolute;
   bottom: 0;
   left: 0;
   right: 0;
   animation: smallWave 6s ease-in-out infinite alternate-reverse;
   width: 100vw;
   height: 7.312vw;
}
@keyframes bigWave {
   from {
      transform: translateY(0);
   }
   to {
      transform: translateY(4.3vw);
   }
}
@keyframes smallWave {
   from {
      transform: translateY(0);
   }
   to {
      transform: translateY(2.44vw);
   }
}