.our-appoach-slider-section {
   position: relative;
   overflow: hidden;
   height: 100vh;
   background: #fff;
   .landing-page-content-wrap {
      .container {
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
      }
   }
   .our-appoach--slider-item {
      width: 100%;
      .our-appoach--item-title {
         text-align: center;
      }
   }
}
.our-appoach-slider--circle-wrap {
   max-width: 375px;
   margin: 30px 0 50px 0;
   position: relative;
   cursor: pointer;
   @media screen and (max-width: 600px) {
      max-width: 300px;
   }
   @media screen and (max-height: 600px) {
      max-width: 300px;
   }
   &:before {
      position: absolute;
      color: #de4f4f;
      font-size: 2.2rem;
      font-weight: 700;
      letter-spacing: 0.71px;
      transform: translate(-50%, -50%);
      animation: numbersOpacity 1s linear;
      @media screen and (max-width: 600px) {
         font-size: 1.9rem;
      }
   }
   &.-first:before {
      content: '01';
      top: 1%;
      left: 77%;
   }
   &.-second:before {
      content: '02';
      bottom: -1%;
      right: 3%;
   }
   &.-third:before {
      content: '03';
      left: -4%;
      top: 50%;
   }
}
@keyframes numbersOpacity {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
.our-appoach-slider--background {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-48%, -42%);
   width: 70%;
}
.our-appoach-slider--circle {
   width: 100%;
   transition: transform .2s linear;
}