.contacts {
   position: absolute;
   bottom: 0;
   left: 50%;
   transform: translateX(-50%);
   padding: 10px 0;
   z-index: 20;
   font-style: normal;
   &.-is-hamburger {
      padding: 5px 15px 15px;
      position: static;
      transform: none;
      .contacts--list {
         align-items: flex-start;
         flex-direction: column;
      }
      .contacts--item {
         margin: 0;
         flex-direction: column;
         align-items: flex-start;
      }
   }
}
.contacts--list {
   display: flex;
   align-items: center;
   margin: 0;
   padding: 0;
   list-style: none;
   font-size: .9rem;
}
.contacts--item {
   margin-right: 2em;
   display: flex;
   align-items: center;
   @media screen and (max-width: 700px) {
      margin-right: 1.5em;
   }
   @media screen and (max-width: 600px) {
      margin: 0 0 10px 0;
   }
   &:last-child {
      margin: 0;
   }
   &.-light {
      .contacts--description-title {
         color: #fff;
      }
      .contacts--description-subtitle {
         color: #fff;
         a {
            color: #fff;
         }
         svg #Logo {
            fill: #ffffff;
         }
      }
      .contacts--description-link,
      .contacts--description-subtitle a {
         &:hover {
            text-shadow: 1px 1px 10px rgba(255, 255, 255, 0.3);
         }
      }
   }
   &.-dark {
      .contacts--description-title {
         color: #12233d;
      }
      .contacts--description-subtitle {
         color: #12233d;
         a {
            color: #12233d;
         }
         svg #Logo {
            fill: #12233d;
         }
      }
      .contacts--description-link,
      .contacts--description-subtitle a {
         &:hover {
            text-shadow: 1px 1px 10px rgba(#12233d, 0.15);
         }
      }
   }
}
.contacts--icon-link {
   margin-right: 10px;
   outline: none;
   @media screen and (max-width: 700px) {
      margin-right: 5px;
   }
   &:active {
      position: relative;
      top: .5px;
   }
   svg {
      width: 1.9rem;
      height: 1.9rem;
      @media screen and (max-width: 700px) {
         width: 1.8rem;
         height: 1.8rem;
      }
   }
}
.contacts--title {
   color: #fff;
   font-size: .9em;
   font-weight: 700;
   letter-spacing: 0.28px;
   margin: 15px 0 7px 0;
}
.contacts--description {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: flex-start;
}
.contacts--description-link {
   text-decoration: none;
   display: block;
   padding: 2px;
   border-radius: 2px;
   transition: background-color .1s ease-in-out;
   &:active {
      position: relative;
      top: .5px;
   }
}
.contacts--description-title {
   font-size: .9em;
   font-weight: 600;
   letter-spacing: 0.39px;
   white-space: nowrap;
   @media screen and (max-width: 700px) and (min-width: 601px) {
      font-size: 0.85em;
   }
}
.contacts--description-subtitle {
   font-size: 1em;
   letter-spacing: 0.39px;
   white-space: nowrap;
   display: flex;
   align-items: center;
   padding: 2px 0;
   margin-left: 2px;
   @media screen and (max-width: 700px) and (min-width: 601px) {
      font-size: 0.85em;
   }
   a {
      text-decoration: none;
      display: flex;
      align-items: center;
      padding: 2px 3px;
      transition: background-color .1s ease-in-out;
      border-radius: 2px;
      &:active {
         position: relative;
         top: .5px;
      }
      svg {
         margin-left: 0.3em;
      }
   }
}