.top-section {
   height: 100vh;
   position: relative;
   z-index: 1;
   overflow: hidden;
   .landing-page-content-wrap {
      .container {
         position: relative;
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
      }
   }
   .landing-page--description {
      max-width: 750px;
      padding-bottom: 30px;
   }
}
.top-section--title {
   color: #fff;
   font-size: 3.7rem;
   font-weight: 700;
   letter-spacing: 1.51px;
   line-height: 1.2;
   text-transform: uppercase;
   margin: 0 auto 0 auto;
   padding-bottom: .6em;
   max-width: 800px;
   text-align: center;
   @media screen and (max-width: 800px) {
      font-size: 3.4rem;
   }
   @media screen and (max-width: 650px) {
      font-size: 2.8rem;
   }
   @media screen and (max-width: 470px) {
      font-size: 2.2rem;
   }
}
.top-section--description {
   color: #fff;
   font-size: 1.25rem;
   line-height: 1.8rem;
   font-weight: 600;
   letter-spacing: 0.43px;
   margin: 0;
   padding-bottom: 1.5em;
   @media screen and (max-width: 650px) {
      font-size: 1.125rem;
   }
   .dash {
      display: inline-block;
      position: relative;
      margin: 0 16px 0 8px;
      width: 22px;
      height: 1px;
      background: currentColor;
      vertical-align: middle;
      transform: translateY(-1px);
      &:after {
         content: '';
         position: absolute;
         width: 6px;
         height: 1px;
         background: currentColor;
         left: auto;
         right: -9px;
         top: 0;
      }
   }
}
.top-section-social-icons {
   position: absolute;
   top: 50%;
   left: 10px;
   transform: translateY(-50%);
   z-index: 2;
}
.top-section-social-icons--list {
   margin: 0;
   padding: 0;
   list-style: none;
}
.top-section-social-icons--item {
   margin-bottom: 10px;
   &:last-child {
      margin-bottom: 0;
   }
}
.top-section-social-icons--link {
   display: flex;
   align-items: center;
   padding: 5px;
   border-radius: 2px;
   text-decoration: none;
   position: relative;
   overflow: hidden;
   &:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      left: 16px;
      width: 0;
      height: 0;
      border-radius: 50%;
      background: #fff;
      z-index: -1;
   }
   &:hover {
      &:before {
         width: 200px;
         height: 200px;
         transition: width .2s ease-in, height .2s ease-in;
      }
      .top-section-social-icons--link-title {
         display: block;
         animation: showText .3s linear;
      }
   }
   svg#upwork {
      .cls-1 {
         fill:#5b659b
      }
   }
   svg#clutch {
      .cls-1 {
         fill:#5b659b;
      }
      .cls-2 {
         fill:#5b659b;
      }
   }
}
.top-section-social-icons--link-title {
   font-size: 14px;
   color: #12233d;
   margin: 0 10px;
   display: none;
}
@keyframes showText {
   from {
      opacity: 0;
   }
   75% {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}