.landing-page {
   background: black;
   .disable-slider {
      .top-section {
         padding-top: 50px;
      }
      & > section.one-screen-slide {
         position: relative;
         height: auto;
      }
      .landing-page-content-wrap {
         height: auto;
         padding: 120px 0;
         & > .container  {
            padding-bottom: 0;
         }
      }
   }
}
.landing-page--title {
   color: #de4f4f;
   font-size: 3rem;
   font-weight: 700;
   letter-spacing: 1.04px;
   line-height: 1.3;
   text-transform: uppercase;
   text-align: center;
   max-width: 800px;
   margin: 0 auto 40px auto;
   @media screen and (max-width: 800px) {
      font-size: 2.5rem;
   }
   @media screen and (max-width: 600px) {
      font-size: 2.1rem;
   }
   &.-white-color {
      color: #fff;
   }
}
.landing-page--description {
   color: #de4f4f;
   font-size: 1.15rem;
   letter-spacing: 0.43px;
   line-height: 1.5;
   max-width: 715px;
   margin: 0 auto 0 auto;
   padding-bottom: 30px;
   text-align: center;
   @media screen and (max-width: 600px) {
      font-size: 1.15rem;
   }
   &:last-of-type {
      padding-bottom: 0;
   }
   &.-white-color {
      color: #fff;
   }
}
.landing-page-gradient-bg {
   background: #051c4f;
   background: linear-gradient(to right, #051c4f 0%,#43315d 100%);;
}
html body .keen-app .landing-page {
   section {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      transition: transform .5s ease-in-out;
      height: 100vh;
   }
}
.landing-page-content-wrap {
   padding: 70px 0 0 0;
   height: 100%;
   overflow-y: auto;
   height: 100vh;
   .container {
      padding-bottom: 70px;
   }

   // Removing scrollbar
   -ms-overflow-style: none;
   overflow: -moz-scrollbars-none;
   scrollbar-width: none;
   &::-webkit-scrollbar {
      width: 0px;
      background: transparent;
      display: none;
   }
}