.our-experiences-section {
   height: 100vh;
   position: relative;
   overflow: hidden;
   .landing-page-content-wrap {
      .container {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
         position: relative;
         z-index: 2;
      }
   }
}
.landing-page--title.-our-experiences {
   margin-bottom: 0;
}
.our-experiences--description {
   color: #de4f4f;
   font-size: 2.25rem;
   font-weight: 700;
   letter-spacing: 0.78px;
   margin: 0 0 40px 0;
   text-align: center;
   @media screen and (max-width: 600px) {
      font-size: 1.8rem;
      margin-bottom: 25px;
   }
}

// Slider
.our-experiences-slider {
   width: calc(100% + 15px);
   margin: -15px;
}
.our-experiences-slider--item {
   padding: 15px;
   margin: 0;
   width: 100%;
   @media screen and (max-width: 600px) {
      display: flex;
      flex-direction: column;
   }
}
.our-experiences-slider--figure {
   display: flex;
   margin: 0 -15px;
   align-items: center;
}
.our-experiences-slider--image-wrap {
   width: 30%;
   padding: 15px;
   img {
      max-width: 100%;
      width: 215px;
      filter: grayscale(100%);
   }
}
.our-experiences-slider--figcaption {
   width: 70%;
   padding: 15px;
}
.our-experiences-slider--quote {
   color: #fff;
   font-size: 1.125rem;
   letter-spacing: 0.39px;
   margin: 0 0 25px 0;
   @media screen and (max-width: 600px) {
      margin-bottom: 15px;
      flex-grow: 1;
   }
}
.our-experiences-slider--author {
   color: #de4f4f;
   font-size: 1.5rem;
   font-weight: 700;
   letter-spacing: 0.52px;
   margin: 0 0 5px 0;
}
.our-experiences-slider--job-title {
   color: #fff;
   font-size: 1.25rem;
   letter-spacing: 0.43px;
   margin: 0;
}