.container {
    width: 100%;
    max-width: 1260px;
    margin: 0 auto;
    padding: 0 50px;
    min-height: 100%;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 600px) {
        padding: 0 15px;
    }
    &.-column-wrap {
        margin: -15px;
    }
    &.-full-width {
        max-width: 1920px;
        padding: 0 30px;
        @media screen and (max-width: 700px) {
            padding: 0 15px;
        }
        @media screen and (max-width: 650px) {
            padding: 0 30px;
         }
    }
}