.our-appoach-list-section {
   position: relative;
   overflow: hidden;
   background: #fff;
   height: 100vh;
   .landing-page-content-wrap {
      .container {
         flex-direction: column;
         display: flex;
         align-items: center;
         justify-content: center;
         max-width: 1150px;
      }
   }
}
.our-appoach-list-section--list {
   padding: 30px 0;
   list-style: none;
   margin: 0;
}
.our-appoach-list-section--item {
   padding-bottom: 60px;
   &:last-child {
      padding-bottom: 0;
   }
   &:nth-child(odd) {
      .our-appoach-list-section--image-wrap {
         @media screen and (min-width: 801px) {
            order: 1;
         }
      }
   }
}
.our-appoach-list-section--figure {
   display: flex;
   align-items: center;
   margin: -15px;
   @media screen and (max-width: 800px) {
      flex-direction: column;
   }
}
.our-appoach-list-section--image-wrap {
   width: 50%;
   text-align: center;
   padding: 15px;
   @media screen and (max-width: 800px) {
      width: 100%;
      margin-bottom: 10px;
      &:last-child {
         margin-bottom: 0;
      }
   }
   &:last-child {
      @media screen and (max-width: 800px) {
         margin-bottom: 0;
      }
   }
   img {
      max-width: 100%;
      max-width: 300px;
   }
}
.our-appoach-list-section--figcaption {
   width: 50%;
   padding: 15px;
   @media screen and (max-width: 800px) {
      width: 100%;
   }
}