.our-appoach-section {
   height: 100vh;
   position: relative;
   overflow: hidden;
   .landing-page-content-wrap {
      .container {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
      }
      .button {
         margin-top: 30px;
      }
   }
}
.our-appoach--item-title {
   color: #de4f4f;
   font-size: 1.875rem;
   font-weight: 700;
   letter-spacing: 0.65px;
   margin: 0 0 30px 0;
}
.our-appoach--item-description {
   margin: 0 auto 20px auto;
   align-self: flex-start;
   max-width: 700px;
   &:last-child {
      margin-bottom: 0;
   }
}