.about-us-section {
   height: 100vh;
   position: relative;
   overflow: hidden;
   background: #fff;
   .landing-page-content-wrap {
      .container {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
      }
   }
}
.about-us-section-counter {
   padding: 30px 0;
   width: 100%;
   max-width: 800px;
   margin: 0 auto;
}
.about-us-section-counter--list {
   display: flex;
	justify-content: space-between;
	padding-bottom: 25px;
	margin: -15px;
   list-style: none;
   padding: 0;
   @media screen and (max-width: 600px) {
      flex-wrap: wrap;
   }
}
.about-us-section-counter--item {
   @media screen and (max-width: 600px) {
      width: 50%;
      display: flex;
      justify-content: center;
   }
}